
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #14af43;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#warning_color{
  border-radius: 50%;
  height: 20px;
  width: 20px;
  margin-right: 1%;
  border: none;
  outline: none;
}

#warning_color::-webkit-color-swatch-wrapper {
  padding: 0; 
}
#warning_color::-webkit-color-swatch {
  border: none;
  border-radius: 50%;
}

textarea:focus { 
  outline: none !important;
  box-shadow: none !important;
}

.Light , .Light:focus{
  color: #fff !important;
  border: 1px solid white !important;
}


.Light::placeholder{
  color: #E8E8E8	 !important;
  opacity: 0.8;
}

.Dark, .Dark:focus{
  color: #000 !important;
  border: 1px solid gray !important;
}

.Dark::placeholder{
  color: gray !important;
}

.btn-dark{
  border: 1px solid white !important;
}